import React, { useState } from 'react'
import questions from './questions'
import './css/test.css'

export default function Main() {
    const [index, setindex] = useState(0)
    const [score, setscore] = useState(0)
    const [toggle, settoggle] = useState(false)

    console.log(questions.length);
    console.log(questions.length - 1);

    const answering = (check) => {
        if (check) {
            setscore(score + 1)
        }
        
        if (index < questions.length - 1) {
            setindex(index + 1)
        } else settoggle(true)

    }

    return (
        <main>
            <section className="section" >
             {toggle ? 
             <article className="score" >
                 <h1 className="showScore" >{score}/{questions.length} </h1>
                 <button className="reset" onClick={()=>{setindex(0);settoggle(false);setscore(0)}}>Retry</button> 
                 </article > : 
            
                 <article className="quiz" >
                 <h1 className="question" >{questions[index].question}</h1>
                 <div className="answers" >
                {questions[index].answers.map((info)=>{
                 const {a,check} = info
                 return (
                    <button onClick={()=>{answering(check)}} >{a}</button>
                 )
             })}</div></article>}
             

            </section>
        </main>
    )
}
